@import "src/styles/settings";

.contacts {
  @extend %main-container;
  height: 70vh;
  background-color: #f8f9fa !important;
  display: grid;
  grid-template-rows: 30vh 40vh;
  justify-content: center;

  @media (max-width: 480px) {
    width: 480px;
    grid-template-rows: 10vh 40vh;
  }

  h1 {
    @extend %page-h1-title
  }

  h3 {
    @extend %page-h3-title
  }

  .contactsContainer {
    display: grid;
    height: 346px;

    @media (max-width: 480px) {
      padding-top: 50px;
    }

    .contactForm {
      margin-left: 10px;
      width: 1156px;

      input {
        font-family: inherit;
        margin: 10px;
        padding-left: 10px;
        width: 394px;
        height: 51px;
        border-radius: 3px;
        outline: none;
        font-size: 16px;
        border: none;

        @media (max-width: 480px) {
          width: 350px;
        }

      }

      textarea {
        padding: 10px;
        border-radius: 3px;
        font-family: inherit;
        width: 818px;
        height: 144px;
        outline: none;
        font-size: 16px;
        border: none;

        @media (max-width: 480px) {
          width: 350px;
        }

      }

      button {
        font-family: inherit;
        width: 200px;
        height: 50px;
        margin-top: 30px;
        border-radius: 30px;
        font-size: 15px;
        outline: none;
        text-decoration: none;
        border: none;
        background-color: $orange_color;
        color: #ffffff;
        cursor: pointer;
        font-weight: bold;
      }

      @media (max-width: 480px) {
        width: 400px;
      }

    }
  }

}